export default ".navbar-spacer {\n  flex: 1 1 auto;\n}\n\n.navbar {\n  z-index: 2;\n}\n\n.navbar-brand {\n  text-decoration: none;\n  color: white;\n}\n\n.navbar-container {\n  display: flex;\n  flex-direction: column;\n  position: absolute;\n  top: 0;\n  bottom: 0;\n  left: 0;\n  right: 0;\n}\n\n.navbar-is-mobile .navbar {\n  position: fixed;\n  /* Make sure the toolbar will stay on top of the content as it scrolls past. */\n  z-index: 2;\n}\n\n.navbar-sidenav-container {\n  /* When the sidenav is not fixed, stretch the sidenav container to fill the available space. This\n           causes `<mat-sidenav-content>` to act as our scrolling element for desktop layouts. */\n  flex: 1;\n}\n\n.navbar-is-mobile .navbar-sidenav-container {\n  /* When the sidenav is fixed, don't constrain the height of the sidenav container. This allows the\n           `<body>` to be our scrolling element for mobile layouts. */\n  flex: 1 0 auto;\n}\n\n/*Set sidenav width*/\n\nmat-sidenav {\n  min-width: 180px !important;\n  border-right: 1px solid #eee;\n  box-shadow: 6px 0 6px rgba(0, 0, 0, .1);\n  /* background-color:rgb(63, 81, 181); */\n}\n\n/* Set height of wrapper to stop content from moving up & down */\n\n.progress-bar-container {\n  height: 5px;\n}\n\na.mat-list-item.active {\n  background: rgba(0, 0, 0, .04);\n}\n\n#push-bottom {\n  position: absolute;\n  bottom: 0;\n}\n/*# sourceMappingURL=data:application/json;base64, */"